"use client";

import React, { useState, useTransition, useCallback } from "react";
import { useSession, signOut } from "@/lib/auth-client";
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import { Alert, AlertDescription } from "@/components/ui/alert";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { 
  User, 
  LogOut, 
  Settings, 
  HelpCircle, 
  Sparkles, 
  Loader2, 
  // AlertCircle, // Not used in current implementation
  RefreshCw,
  Wifi
} from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { cn } from "@/lib/utils";

interface SignOutState {
  isLoading: boolean;
  error?: string;
  canRetry: boolean;
}

export const UserProfile = React.memo(function UserProfile() {
  const { data: session, isPending, error } = useSession();
  const router = useRouter();
  const [isPendingAction, startTransition] = useTransition();
  const [signOutState, setSignOutState] = useState<SignOutState>({
    isLoading: false,
    canRetry: false
  });

  // Debug logging for session state
  React.useEffect(() => {
    if (process.env.NODE_ENV === 'development') {
      console.log('UserProfile session state:', { 
        session, 
        isPending, 
        error,
        hasData: !!session 
      });
    }
  }, [session, isPending, error]);

  // Add timeout for persistent loading states
  const [loadingTimeout, setLoadingTimeout] = React.useState(false);
  React.useEffect(() => {
    if (isPending) {
      const timer = setTimeout(() => {
        console.warn('Auth session loading timeout - this may indicate an issue with the auth configuration');
        setLoadingTimeout(true);
      }, 10000); // 10 second timeout
      
      return () => clearTimeout(timer);
    } else {
      setLoadingTimeout(false);
    }
  }, [isPending]);

  // Enhanced sign out with error handling and retry
  const handleSignOut = useCallback(async () => {
    startTransition(async () => {
      try {
        setSignOutState({ isLoading: true, canRetry: false });
        
        await signOut();
        router.replace("/");
      } catch (error) {
        console.error("Sign out error:", error);
        
        const errorMessage = error instanceof Error ? error.message : '砖  爪驻';
        setSignOutState({
          isLoading: false,
          error: `砖 转转拽转: ${errorMessage}`,
          canRetry: true
        });
      }
    });
  }, [router, startTransition]);

  // Retry sign out
  const handleRetrySignOut = useCallback(() => {
    setSignOutState({ isLoading: false, canRetry: false });
    handleSignOut();
  }, [handleSignOut]);

  if (isPending) {
    return (
      <div className="flex items-center gap-3" role="status" aria-label="注 驻专驻 砖转砖">
        <div className="relative">
          <Skeleton className="h-10 w-10 rounded-full" />
          {/* Animated loading ring */}
          <div className="absolute inset-0 rounded-full border-2 border-primary/20 border-t-primary/50 animate-spin" aria-hidden="true"></div>
        </div>
        <div className="hidden md:block space-y-2">
          <Skeleton className="h-3 w-24 animate-pulse" />
          <Skeleton className="h-2 w-20 animate-pulse" style={{ animationDelay: '0.2s' }} />
        </div>
        
        {/* Loading timeout warning */}
        {loadingTimeout && (
          <div className="fixed top-4 end-4 z-50">
            <Alert variant="destructive" className="w-80" role="alert" aria-live="assertive">
              <div className="flex items-start gap-2">
                <Wifi className="h-4 w-4 mt-0.5 flex-shrink-0" aria-hidden="true" />
                <div className="flex-1 space-y-2">
                  <AlertDescription>
                    注转 转 砖转砖 拽转  专 爪驻. 转 砖 注 转专转 砖专转.
                  </AlertDescription>
                </div>
              </div>
            </Alert>
          </div>
        )}
        
        {/* Screen reader loading announcement */}
        <div className="sr-only" aria-live="polite">
          注 驻专 砖转砖...
        </div>
      </div>
    );
  }

  // Handle error state
  if (error) {
    return (
      <div className="flex items-center gap-3" role="status" aria-label="砖 注转 驻专驻 砖转砖">
        <Alert variant="destructive" className="w-80" role="alert" aria-live="assertive">
          <div className="flex items-start gap-2">
            <Wifi className="h-4 w-4 mt-0.5 flex-shrink-0" aria-hidden="true" />
            <div className="flex-1 space-y-2">
              <AlertDescription>
                砖 注转 转 砖转砖: {error.message || '砖  注'}
              </AlertDescription>
            </div>
          </div>
        </Alert>
      </div>
    );
  }

  if (!session) {
    return (
      <Link href="/auth">
        <Button 
          className="group relative overflow-hidden transition-all duration-200 hover:scale-105"
          aria-label="转专转 砖"
        >
          <span className="relative z-10 flex items-center gap-2">
            <User className="h-4 w-4" aria-hidden="true" />
            转专转
          </span>
          <div className="absolute inset-0 bg-gradient-to-r from-primary/80 to-primary opacity-0 group-hover:opacity-100 transition-all duration-300" aria-hidden="true" />
          
          {/* Subtle shimmer effect */}
          <div className="absolute inset-0 -skew-x-12 bg-gradient-to-r from-transparent via-white/10 to-transparent opacity-0 group-hover:opacity-100 group-hover:animate-shimmer transition-opacity duration-500" aria-hidden="true"></div>
        </Button>
      </Link>
    );
  }

  const userInitials = (
    session.user?.name?.split(" ").map(n => n[0]).join("") ||
    session.user?.email?.[0] ||
    ""
  ).toUpperCase();

  return (
    <>
      {/* Error display for sign-out failures */}
      {signOutState.error && (
        <div className="fixed top-4 end-4 z-50">
          <Alert variant="destructive" className="w-80" role="alert" aria-live="assertive">
            <div className="flex items-start gap-2">
              <Wifi className="h-4 w-4 mt-0.5 flex-shrink-0" aria-hidden="true" />
              <div className="flex-1 space-y-2">
                <AlertDescription>{signOutState.error}</AlertDescription>
                {signOutState.canRetry && (
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={handleRetrySignOut}
                    className="h-7 px-2 text-xs"
                    aria-label="住 砖 转转拽"
                  >
                    <RefreshCw className="h-3 w-3 me-1" aria-hidden="true" />
                    住 砖
                  </Button>
                )}
              </div>
            </div>
          </Alert>
        </div>
      )}

      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button 
            variant="ghost" 
            className={cn(
              "relative h-10 w-10 rounded-full p-0 hover:bg-primary/10 group transition-all duration-200",
              (signOutState.isLoading || isPendingAction) && "opacity-75"
            )}
            disabled={signOutState.isLoading || isPendingAction}
            aria-label="转驻专 砖转砖 - 驻转 驻砖专转 砖"
            aria-expanded="false"
            aria-haspopup="menu"
          >
            <Avatar className="h-9 w-9 transition-all group-hover:scale-105">
              <AvatarImage
                src={session.user?.image || ""}
                alt={session.user?.name || "砖转砖"}
                referrerPolicy="no-referrer"
              />
              <AvatarFallback className="bg-gradient-to-br from-primary/20 to-primary/10 text-primary font-semibold">
                {userInitials}
              </AvatarFallback>
            </Avatar>
            
            {/* Loading overlay */}
            {(signOutState.isLoading || isPendingAction) && (
              <div className="absolute inset-0 bg-background/50 rounded-full flex items-center justify-center">
                <Loader2 className="h-4 w-4 animate-spin text-primary" aria-hidden="true" />
                <span className="sr-only">注 驻注...</span>
              </div>
            )}
            
            {/* Online indicator */}
            <div className={cn(
              "absolute bottom-0 end-0 h-3 w-3 bg-green-500 rounded-full border-2 border-background transition-opacity duration-200",
              (signOutState.isLoading || isPendingAction) && "opacity-50"
            )} aria-label="专" />
          </Button>
        </DropdownMenuTrigger>
        
        <DropdownMenuContent align="end" className="w-64 p-2" role="menu">
          <DropdownMenuLabel className="font-normal p-3 bg-gradient-to-r from-primary/5 to-primary/10 rounded-lg mb-2" role="banner">
            <div className="flex items-center gap-3">
              <Avatar className="h-12 w-12">
                <AvatarImage
                  src={session.user?.image || ""}
                  alt={session.user?.name || "砖转砖"}
                  referrerPolicy="no-referrer"
                />
                <AvatarFallback className="bg-gradient-to-br from-primary/20 to-primary/10 text-primary font-semibold text-lg">
                  {userInitials}
                </AvatarFallback>
              </Avatar>
              <div className="flex-1 space-y-1">
                <p className="text-sm font-semibold leading-none">
                  {session.user?.name || "砖转砖"}
                </p>
                <p className="text-xs text-muted-foreground" dir="ltr">
                  {session.user?.email}
                </p>
                <Badge variant="secondary" className="text-xs px-2 py-0" role="status" aria-label="住住 砖: 砖转砖 驻专">
                  <Sparkles className="me-1 h-3 w-3" aria-hidden="true" />
                  砖转砖 驻专
                </Badge>
              </div>
            </div>
          </DropdownMenuLabel>
          
          <DropdownMenuSeparator className="my-2" />
          
          <DropdownMenuItem 
            asChild 
            className={cn(
              "cursor-pointer p-3 hover:bg-primary/5 transition-colors duration-200",
              (signOutState.isLoading || isPendingAction) && "opacity-50 pointer-events-none"
            )}
            role="menuitem"
          >
            <Link href="/profile" className="flex items-center gap-3" aria-label="注专 祝 驻专驻 砖">
              <User className="h-4 w-4 text-primary" aria-hidden="true" />
              <span>驻专驻 砖</span>
            </Link>
          </DropdownMenuItem>
          
          <DropdownMenuItem 
            asChild 
            className={cn(
              "cursor-pointer p-3 hover:bg-primary/5 transition-colors duration-200",
              (signOutState.isLoading || isPendingAction) && "opacity-50 pointer-events-none"
            )}
            role="menuitem"
          >
            <Link href="/settings" className="flex items-center gap-3" aria-label="注专 祝 专转 砖">
              <Settings className="h-4 w-4 text-primary" aria-hidden="true" />
              <span>专转</span>
            </Link>
          </DropdownMenuItem>
          
          <DropdownMenuItem 
            asChild 
            className={cn(
              "cursor-pointer p-3 hover:bg-primary/5 transition-colors duration-200",
              (signOutState.isLoading || isPendingAction) && "opacity-50 pointer-events-none"
            )}
            role="menuitem"
          >
            <Link href="/help" className="flex items-center gap-3" aria-label="注专 祝 注专 转">
              <HelpCircle className="h-4 w-4 text-primary" aria-hidden="true" />
              <span>注专 转</span>
            </Link>
          </DropdownMenuItem>
          
          <DropdownMenuSeparator className="my-2" />
          
          <DropdownMenuItem 
            onClick={handleSignOut} 
            disabled={signOutState.isLoading || isPendingAction}
            className={cn(
              "cursor-pointer p-3 text-red-600 hover:bg-red-50 hover:text-red-700 focus:bg-red-50 focus:text-red-700 transition-colors duration-200 relative",
              (signOutState.isLoading || isPendingAction) && "opacity-75"
            )}
            role="menuitem"
            aria-label="转转拽转 砖"
            aria-describedby={(signOutState.isLoading || isPendingAction) ? "signout-status" : undefined}
          >
            <div className="flex items-center gap-2">
              <div className="relative">
                {(signOutState.isLoading || isPendingAction) ? (
                  <Loader2 className="h-4 w-4 animate-spin" aria-hidden="true" />
                ) : (
                  <LogOut className="h-4 w-4" aria-hidden="true" />
                )}
              </div>
              <span>
                {(signOutState.isLoading || isPendingAction) ? "转转拽..." : "转转拽转"}
              </span>
              {(signOutState.isLoading || isPendingAction) && (
                <span id="signout-status" className="sr-only">
                  爪注 转转拽转...
                </span>
              )}
            </div>
          </DropdownMenuItem>
        </DropdownMenuContent>
        
        {/* Screen reader announcements */}
        <div className="sr-only" aria-live="polite" aria-atomic="true">
          {(signOutState.isLoading || isPendingAction) && "转转拽..."}
          {signOutState.error && `砖 转转拽转: ${signOutState.error}`}
        </div>
      </DropdownMenu>
    </>
  );
});